body {
  margin: 0;
  min-width: 320px;
  font-family: "Raleway", "Open Sans", sans-serif;
  background-color: #141414;
  color: #F0F0F0;
  font-size: 15px;
  line-height: 22px; }

a, a span {
  color: #F0F0F0;
  transition: 0.3s;
  transform: translateZ(0); }

img {
  max-width: 100%; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/ralewayregular.woff2") format("woff2"), url("../fonts/ralewayregular.woff") format("woff");
