.wrapper {
  max-width: 1080px;
  height: 100vh;
  padding: 0 32px;
  margin-left: auto;
  margin-right: auto; }

@media (max-width: 766px) {
  .wrapper {
    padding: 0 16px; } }

.choose-quest h1 {
  margin-top: 0;
  margin-bottom: 48px;
  font-size: 64px;
  line-height: 70px;
  letter-spacing: 0.01em;
  color: #ffffff; }

.quests-filters {
  margin-bottom: 39px; }

.choose-quest__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-right: -2.15%; }

.choose-quest__list-wrapper {
  display: flex;
  flex-wrap: wrap;
  font-size: 0; }

.choose-quest__item {
  margin-bottom: 32px;
  box-sizing: border-box;
  width: calc(100% / 3 - 2.15%);
  margin-right: 2.15%; }

@media (max-width: 1023px) {
  .choose-quest__item {
    width: calc(100% / 2 - 2.15%); } }

@media (max-width: 767px) {
  .choose-quest h1 {
    margin-bottom: 44px;
    font-size: 36px;
    line-height: 40px; }
  .choose-quest__item {
    width: 100%;
    margin-bottom: 16px; } }

.quests-filters ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  overflow: hidden; }

.quests-filters li {
  display: flex;
  position: relative;
  padding-right: 3.7%; }
  .quests-filters li:not(:first-child) {
    padding-left: 3.7%;
    border-left: 1px solid rgba(255, 255, 255, 0.32); }
  .quests-filters li:first-child {
    justify-content: flex-start; }
  .quests-filters li.active span {
    border-bottom: 2px solid #F2890F; }

.quests-filters svg {
  margin-right: 13px; }

.quests-filters a {
  display: flex;
  align-items: center;
  text-decoration: none;
  height: 40px; }
  .quests-filters a:hover span {
    color: #F2890F; }

.quests-filters span {
  white-space: nowrap;
  font-size: 14px;
  line-height: 23px;
  color: #F6F6F6;
  transition: 0.3s; }

@media (max-width: 1023px) {
  .quests-filters ul {
    position: relative; }
    .quests-filters ul::after {
      content: '';
      position: absolute;
      display: block;
      top: 0;
      right: 0px;
      bottom: 0;
      width: 40px;
      background: linear-gradient(270deg, #1C1B1B 0%, rgba(28, 27, 27, 0) 100%); } }

.page-nav {
  margin-bottom: 7px; }
  .page-nav a {
    font-family: "Raleway", "Open Sans", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #F2890F;
    text-decoration: none; }

.quest-item {
  position: relative;
  position: relative;
  font-family: "Raleway", "Open Sans", sans-serif;
  color: #F0F0F0; }
  .quest-item h3 {
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: bold;
    line-height: 29px;
    color: #ffffff; }

.quest-item__inner {
  display: block;
  padding-top: calc(232 / 344 * 100%);
  height: 0;
  width: 100%;
  position: relative; }

.quest-item__info {
  font-size: 13px;
  line-height: 19px; }
  .quest-item__info p {
    display: flex;
    align-items: center;
    margin-top: 0px;
    margin-bottom: 0; }
  .quest-item__info span {
    font-weight: 500;
    opacity: 0.8; }
  .quest-item__info svg {
    margin-right: 10px; }
  .quest-item__info img {
    width: 100%;
    height: 100%; }

.quests-item__specification {
  display: flex;
  flex-direction: row;
  margin-top: 6px; }
  .quests-item__specification p:not(:first-child) {
    position: relative;
    margin-left: 25px; }
    .quests-item__specification p:not(:first-child)::before {
      content: '';
      display: block;
      position: absolute;
      left: -12px;
      width: 1px;
      height: 20px;
      background-color: #F0F0F0;
      opacity: 0.5; }

.quest-item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  width: 100%;
  height: 100%; }
  .quest-item__img img {
    width: 100%;
    height: 100%; }
  .quest-item__img::after {
    content: '';
    position: absolute;
    top: 30%;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(0.17deg, rgba(28, 27, 27, 0.9) 5.23%, rgba(46, 46, 46, 0) 98.38%); }

.quest-item__text {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 20px 19px; }
  .quest-item__text #icon-pin svg {
    viewBox: '11 14'; }

.quest-item__hit {
  display: none;
  align-items: flex-start;
  position: absolute;
  top: 15px;
  right: 18px;
  font-size: 12px;
  line-height: 17px;
  padding: 4px 12px;
  border: 2px solid rgba(242, 137, 15, 0.74);
  border-radius: 25px; }
  .quest-item__hit svg {
    margin-right: 4px; }

.quest-item--hit .quest-item__hit {
  display: flex; }
